.search-item__link {
  display: block;
  box-sizing: border-box;
  width: 320px;
  color: $black;
  padding: 28px 55px 25px 32px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: auto;
    padding: 36px 31px 0 33px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 36px 32px 0 32px;
  }

  &--hidden {
    @media (min-width: $tablet-width) {
      display: flex;
      min-height: 88px;
      font-size: 0;
    }
  }

  &:hover {
    color: rgba($black, 0.6);
  }

  &:active {
    color: rgba($black, 0.3);
  }
}

.search-item__icon {
  opacity: 0.2;
  margin-right: 21px;

  @media (min-width: $tablet-width) {
    margin-right: 0;
  }
}

.search-item__link:hover .search-item__icon {
  opacity: 1;
  fill: $lightnes-green;
}

.search-item__link:active .search-item__icon {
  opacity: 0.3;
}
